import { parse_regex_from_string } from '@server/util/parse_regex_from_string';

import dedent from 'dedent';
import * as z from 'zod';

type Worldbook_entry = z.infer<typeof Worldbook_entry>;
const Worldbook_entry = z.object({
  name: z.string(),
  uid: z.number().optional().describe(', '),
  enabled: z.boolean(),

  strategy: z
    .object({
      type: z.enum(['constant', 'selective', 'vectorized']).describe(
        dedent(`
          :
          - constant: , .  """%" .
          - selective: , . ,  \`keys\` 
          - vectorized: . 
        `),
      ),
      keys: z
        .array(z.string().transform(string => parse_regex_from_string(string) ?? string))
        .min(1)
        .optional()
        .describe(': '),
      keys_secondary: z
        .object({
          logic: z.enum(['and_any', 'and_all', 'not_all', 'not_any']).describe(
            dedent(`
              :
              - and_any: 
              - and_all: 
              - not_all: 
              - not_any: 
            `),
          ),
          keys: z.array(z.string().transform(string => parse_regex_from_string(string) ?? string)).min(1),
        })
        .optional()
        .describe(
          ': ,  `keys` ,  `logic`  `keys`',
        ),
      scan_depth: z
        .union([z.literal('same_as_global'), z.number().min(1)])
        .optional()
        .describe(': 1 , 2 , '),
    })
    .superRefine((data, context) => {
      if (data.type === 'selective' && data.keys === undefined) {
        context.addIssue({
          code: 'custom',
          path: ['keys'],
          message: " (`'selective'`) , `keys` ",
        });
      }
    })
    .describe(': '),

  position: z
    .object({
      type: z
        .enum([
          'before_character_definition',
          'after_character_definition',
          'before_example_messages',
          'after_example_messages',
          'before_author_note',
          'after_author_note',
          'at_depth',
        ])
        .describe(
          dedent(`
            :
            - before_character_definition: 
            - after_character_definition: 
            - before_example_messages: 
            - after_example_messages: 
            - before_author_note: 
            - after_author_note: 
            - at_depth: 
          `),
        ),
      role: z
        .enum(['system', 'assistant', 'user'])
        .optional()
        .describe(",  `'at_depth'` "),
      depth: z.number().optional().describe(",  `'at_depth'` "),
      order: z.number(),
    })
    .describe(': ')
    .superRefine((data, context) => {
      if (data.type === 'at_depth') {
        if (data.role === undefined) {
          context.addIssue({
            code: 'custom',
            path: ['role'],
            message: " (`position`)  `'at_depth'` ,  `role`",
          });
        }
        if (data.depth === undefined) {
          context.addIssue({
            code: 'custom',
            path: ['depth'],
            message: " (`position`) `'at_depth'` ,  `depth`",
          });
        }
      } else {
        if (data.role !== undefined) {
          context.addIssue({
            code: 'custom',
            path: ['role'],
            message: " (`position`)  `'at_depth'` , `role` , ",
          });
        }
        if (data.depth !== undefined) {
          context.addIssue({
            code: 'custom',
            path: ['depth'],
            message: " (`position`)  `'at_depth'` , `depth` , ",
          });
        }
      }
    }),

  probability: z.number().min(0).max(100).optional(),

  recursion: z
    .object({
      prevent_incoming: z.boolean().describe(''),
      prevent_outgoing: z.boolean().describe(''),
      delay_until: z.number().min(1).nullable().describe(' n '),
    })
    .partial()
    .optional()
    .describe(', '),

  effect: z
    .object({
      sticky: z
        .number()
        .min(1)
        .nullable()
        .describe(': ,  n , %'),
      cooldown: z.number().min(1).nullable().describe(': ,  n '),
      delay: z.number().min(1).nullable().describe(':  n , '),
    })
    .partial()
    .optional(),

  extra: z.record(z.string(), z.any()).optional().describe(': '),

  content: z.string(),
});

export type Worldbook = z.infer<typeof Worldbook>;
export const Worldbook = z.object({ entries: z.array(Worldbook_entry).min(1) });
