import dedent from 'dedent';
import * as z from 'zod';

const Prompt_normal = z
  .object({
    name: z.string(),
    id: z.never().optional(),
    enabled: z.boolean(),

    position: z
      .union([z.literal('relative'), z.number()])
      .default('relative')
      .optional()
      .describe(': `relative` , '),

    role: z.enum(['system', 'user', 'assistant']).default('system').optional(),
    content: z.string(),

    extra: z.record(z.string(), z.any()).optional().describe(': '),
  })
  .transform(data => ({
    ...data,
    id: _.uniqueId(),
  }))
  .describe('');

const prompt_rolable_placeholder_ids = <const>[
  'world_info_before',
  'persona_description',
  'char_description',
  'char_personality',
  'scenario',
  'world_info_after',
];
const prompt_unrolable_placeholder_ids = <const>['dialogue_examples', 'chat_history'];
export const prompt_placeholder_ids = <const>[...prompt_rolable_placeholder_ids, ...prompt_unrolable_placeholder_ids];
const Prompt_placeholder = z
  .object({
    name: z.never().optional(),
    id: z.enum(prompt_placeholder_ids).describe(
      dedent(`
        , 
        - world_info_before: 
        - persona_description: .  user 
        - char_description: . 
        - char_personality: . , 
        - scenario: . , 
        - world_info_after: 
        - dialogue_examples: . , 
        - chat_history: 
      `),
    ),
    enabled: z.boolean(),
    position: z
      .union([z.literal('relative'), z.number()])
      .default('relative')
      .optional()
      .describe(': `relative` , '),

    role: z.enum(['system', 'user', 'assistant']).optional(),
    content: z.never().optional(),

    extra: z.record(z.string(), z.any()).optional().describe(': '),
  })
  .superRefine((data, context) => {
    if (_.includes(prompt_unrolable_placeholder_ids, data.id) && data.role !== undefined) {
      context.addIssue({
        code: 'custom',
        message: ` '${data.id}'  (\`role\`)`,
        path: ['role'],
      });
    }
  })
  .transform(data => ({
    ...data,
    role: data.role ?? 'system',
    name: {
      world_info_before: 'World Info (before) - ',
      persona_description: 'Persona Description - ',
      char_description: 'Char Description - ',
      char_personality: 'Char Personality - ',
      scenario: 'Scenario - ',
      world_info_after: 'World Info (after) - ',
      dialogue_examples: 'Chat Examples - ',
      chat_history: 'Chat History - ',
    }[data.id],
  }))
  .describe(', ');

const Prompt = z.union([Prompt_normal, Prompt_placeholder]);

export type Preset = z.infer<typeof Preset>;
export const Preset = z.object({
  settings: z.object({
    max_context: z
      .number()
      .min(0)
      .max(2000000)
      .describe(
        ' token .  token ,  token ,  2000000',
      ),
    max_completion_tokens: z.number().min(0).describe(' token '),
    reply_count: z.number().min(1).default(1).optional().describe(''),

    should_stream: z.boolean().describe(''),

    temperature: z.number().min(0).max(2).describe(''),
    frequency_penalty: z.number().min(-2).max(2).describe(''),
    presence_penalty: z.number().min(-2).max(2).describe(''),
    top_p: z.number().min(0).max(1),
    repetition_penalty: z.number().min(1).max(2).default(1).optional().describe(''),
    min_p: z.number().min(0).max(1).default(0).optional(),
    top_k: z.number().min(0).max(500).default(0).optional(),
    top_a: z.number().min(0).max(1).default(0).optional(),

    seed: z.number().default(-1).optional().describe(', -1 '),

    squash_system_messages: z.boolean().describe(': '),

    reasoning_effort: z
      .enum(['auto', 'min', 'low', 'medium', 'high', 'max'])
      .describe(', . ,  gemini-2.5-flash,  `min` '),
    request_thoughts: z
      .boolean()
      .default(true)
      .optional()
      .describe(
        ': ; , ',
      ),
    request_images: z.boolean().default(true).optional().describe(': '),
    enable_function_calling: z
      .boolean()
      .default(true)
      .optional()
      .describe(': ;  cursor '),
    enable_web_search: z.boolean().default(true).optional().describe(': '),

    allow_sending_images: z
      .enum(['disabled', 'auto', 'low', 'high'])
      .default('auto')
      .optional()
      .describe(''),
    allow_sending_videos: z.boolean().default(true).optional().describe(''),

    character_name_prefix: z
      .enum(['none', 'default', 'content', 'completion'])
      .default('none')
      .optional()
      .describe(
        dedent(`
        : , 
        - none: 
        - default: ,  \`content\`  ( \`: \`)
        - content: ,  \`content\`  ( \`: \`)
        - completion: ,  \`name\` ; ,  ClaudeGoogle 
      `),
      ),

    wrap_user_messages_in_quotes: z
      .boolean()
      .default(false)
      .optional()
      .describe(': , '),
  }),

  prompts: z
    .array(Prompt)
    .superRefine((data, context) => {
      const duplicate_ids = _(data)
        .filter(prompt => _.includes(prompt_placeholder_ids, prompt.id))
        .groupBy('id')
        .filter(group => group.length > 1)
        .keys()
        .value();
      if (duplicate_ids.length > 0) {
        context.addIssue({
          code: 'custom',
          message: ` id: ${duplicate_ids.join(', ')}`,
        });
      }

      const unused_ids = _.reject(prompt_placeholder_ids, id => data.some(prompt => _.get(prompt, 'id') === id));
      if (unused_ids.length > 0) {
        context.addIssue({
          code: 'custom',
          message: ` id: ${unused_ids.join(', ')}`,
        });
      }

      const disabled_ids = _(data)
        .filter(prompt => _.includes(prompt_placeholder_ids, prompt.id) && prompt.enabled === false)
        .map(prompt => _.get(prompt, 'id'))
        .value();
      if (disabled_ids.length > 0) {
        context.addIssue({
          code: 'custom',
          message: `: ${disabled_ids.join(', ')}`,
        });
      }
    })
    .describe(''),
  prompts_unused: z.array(Prompt).describe(', '),

  extensions: z.record(z.string(), z.any()).optional().describe(': '),
});
